import React from 'react';
import Typography from '@material-ui/core/Typography'
import Grid from '@material-ui/core/Grid'
import TextField from '@material-ui/core/TextField'
import Button from '@material-ui/core/Button';

import {Mail} from '@material-ui/icons'
import firebase from "firebase"

import axios from 'axios'

class ExampleComponent extends React.Component {
    state = {email: "", message: "", success: null, email_success: ""}

    handleChange = (e) => {
      this.setState({ [e.target.name]: e.target.value})
    }

    sendMail = (e) => {
      e.preventDefault()
      console.log("send mail called");
      // axios.post('https://peaceful-wildwood-29980.herokuapp.com/send/mail', {email: this.state.email, message: this.state.message})
      // .then((res) => {
      //   this.setState({email_success: res.data.msg})
      //   console.log("email", res.data.msg);
      // })
      // .catch(err => {
      //   this.setState({success: false})
      //   console.log("err", err);
      // })

      firebase.database().ref('messages').push()
      .set({email : this.state.email, message : this.state.message})
      .then(() => this.setState({ email_success : "Email sent successfully!", success: true}))
      .catch(err => this.setState({ email_success : "Error sending email. Please try again", success: false}))
    }

    render() {
    return <div style={{marginLeft: '5%', width: '90%', height: window.innerHeight}}>
      <Typography variant="h2" gutterBottom color="primary">
        Contact
      </Typography>
      <div style={{width: '95%', marginLeft: '2.5%'}}>
        <Typography variant={window.innerWidth < 700 ? 'h5' : 'h3'} style={{color: "#DA2FD0", fontFamily: "serif", margin: 15}}>
          Let's create something together!
        </Typography>
        <br/>
        <Grid items container spacing={32}>
          <Grid item lg={6} sm={6} xs={12}>
            <Typography variant="h5" style={{color: "aqua"}}>
              Message me
            </Typography>
            <br/>
            <TextField
              name="email"
              label="Email"
              value={this.state.email}
              onChange={this.handleChange}
              style={{color: "#DA2FD0", margin: 10, backgroundColor: "white", width: "80%"}}
              required
              />
            <br/>
            <TextField
              name="message"
              label="Message"
              value={this.state.message}
              onChange={this.handleChange}
              style={{fontColor: "#DA2FD0", margin: 10, backgroundColor: "white", width: "80%"}}
              multiline
              minRow={5}
              rows={5}
              required
              />
            <br/>
            <Button variant="outlined" disabled={!this.state.email || !this.state.message} style={{color: !this.state.email || !this.state.message ? "violet" : "rgb(218, 47, 208)", borderColor: !this.state.email || !this.state.message ? "violet" : "rgb(218, 47, 208)", margin: 10}} onClick={this.sendMail}>Send</Button>
              <Typography variant="h6" style={{color: "violet", margin: 10}}>
                {this.state.email_success}
              </Typography>
          </Grid>
          <Grid item lg={6} sm={6} xs={12}>
            <div style={{marginTop: 50}}>
              <Typography variant="h6" style={{color: "aqua", margin: 10, fontSize: window.innerWidth < 700 ? 15 : 18}}>
                City: Pune
              </Typography>
              <Typography variant="h6" style={{color: "aqua", margin: 10, fontSize: window.innerWidth < 700 ? 15 : 18}}>
                Phone: +91 7498299914
              </Typography>
              <Typography variant="h6" style={{color: "aqua", margin: 10, fontSize: window.innerWidth < 700 ? 15 : 18}}>
                Gmail: kanchan.nannavare@gmail.com
              </Typography>
            </div>
          </Grid>
        </Grid>
      </div>
      </div>
    }


}

export default ExampleComponent;
